// @ts-nocheck
import { useEffect, useState } from "react";
import Printbox from "./components/Printbox";
import Setting from "./components/Setting";
import Status from "./components/Status";
import Tasks from "./components/Tasks";
import "./css/FA5PRO-master/css/all.css";
import { io } from "socket.io-client";
import useGetFetch from "./customHooks/useGetFetch";
import Pending from "./components/Pending";
import usePostFetch from "./customHooks/usePostFetch";
import usePostFormData from "./customHooks/usePostFormData";

function App() {
  const socket = io(process.env.REACT_APP_SERVERURL, { autoConnect: false });

  const {
    data: papers,
    setdata: setpapers,
    IsPending: papersIspending,
    get: getPapers,
    err: paperserr,
  } = useGetFetch();
  const {
    data: inc,
    setdata: setinc,
    IsPending: incIspending,
    get: getinc,
    err: incerr,
  } = useGetFetch();
  const {
    data: state,
    setdata: setstate,
    IsPending: stateIspending,
    get: getstate,
    err: stateerr,
  } = useGetFetch();

  const {
    data: printData,
    IsPending: printIspending,
    post: printPost,
    err: printerr,
  } = usePostFetch();
  const { data: printfiledata, post: printfilePost } = usePostFormData();

  const {
    data: pendingTasks,
    setdata: setpendingTasks,
    IsPending: pendingTasksIspending,
    get: getpendingTasks,
    err: pendingTaskserr,
  } = useGetFetch();
  const {
    data: finishedTasks,
    setdata: setfinishedTasks,
    IsPending: finishedTasksIspending,
    get: getfinishedTasks,
    err: finishedTaskserr,
  } = useGetFetch();

    const {
      data: stats,
      setdata: setstats,
      IsPending: statsIspending,
      get: getstats,
      err: statserr,
    } = useGetFetch();

  const [text, settext] = useState("");
  const [font, setfont] = useState("Arial");
  const [fontsize, setfontsize] = useState(8);
  const [italic, setitalic] = useState(false);
  const [bold, setbold] = useState(false);
  const [underline, setunderline] = useState(false);
  const [alignment, setalignment] = useState("L");
  const [color, setcolor] = useState("#000000");
  const [file, setfile] = useState(null);
  function print() {
    let payload = {};
    payload.text = text;
    payload.font = font;
    payload.fontsize = fontsize;
    italic && (payload.italic = italic);
    bold && (payload.bold = bold);
    payload.alignment = alignment;
    payload.color = color;
    underline && (payload.underline = underline);
    printPost("/print", JSON.stringify({ payload }));
  }
  function printfile() {
    const formdata = new FormData();
    file && formdata.append("file", file);
    formdata.append("font", font);
    formdata.append("fontsize", fontsize);
    italic && formdata.append("italic", italic);
    bold && formdata.append("bold", bold);
    formdata.append("alignment", alignment);
    formdata.append("color", color);
    underline && formdata.append("underline", underline);
    printfilePost("/printfile", formdata);
  }
  useEffect(() => {
    getPapers("/papers");
    getinc("/inc");
    getstate("/state");
    getpendingTasks("/pendingtasks");
    getfinishedTasks("/finishedtasks");
    getstats("/stats")
   

    socket.connect();
    socket.on("connect", () => {
      console.log("connect");
    });
    socket.on("stateUpdate", (data) => {
      setstate(data);
    });
    socket.on("paperUpdate", (data) => {
      console.log(data);
      setpapers(data);
    });
    socket.on("incUpdate", (data) => {
      setinc(data);
    });
    socket.on("updateTaskQueue", (data) => {
      setpendingTasks(data);
    });
    socket.on("updateFinishedTasks", (data) => {
      setfinishedTasks(data);
    });
    socket.on("updateStats",(data)=>{
      setstats(data)
    });

    return () => {
      socket.disconnect();
    };
  }, []);
  return (
    <div className="App relative">
      {(papersIspending || incIspending || stateIspending) && <Pending />}
      <div className="flex justify-between">
        <div className="w-1/5">
          <Setting
            font={font}
            setfont={setfont}
            fontsize={fontsize}
            setfontsize={setfontsize}
            italic={italic}
            setitalic={setitalic}
            bold={bold}
            setbold={setbold}
            underline={underline}
            setunderline={setunderline}
            alignment={alignment}
            setalignment={setalignment}
            color={color}
            setcolor={setcolor}
            state={state}
            papers={papers}
            stats={stats}
          />
        </div>
        <div className="w-3/5">
          <Status state={state?.state} inc={inc?.inc} papers={papers?.papers} />
          <Printbox
            text={text}
            settext={settext}
            print={print}
            printfile={printfile}
            file={file}
            setfile={setfile}
          />
        </div>
        <div className="w-1/5">
          <Tasks
            state={state}
            pendingTasks={pendingTasks?.queue || []}
            finishedTasks={finishedTasks?.finishedTasks || []}
          />
        </div>
      </div>
    </div>
  );
}

export default App;
