// @ts-nocheck
import React, { useEffect, useRef, useState } from "react";
import { toast } from "react-toastify";
import useGetFetch from "../customHooks/useGetFetch";
import usePostFetch from "../customHooks/usePostFetch";
import usePostFormData from "../customHooks/usePostFormData";
import usePutFetch from "../customHooks/usePutFetch";
import DropBox from "./DropBox";

function Setting({
  font,
  setfont,
  fontsize,
  setfontsize,
  italic,
  setitalic,
  underline,
  setunderline,
  bold,
  setbold,
  alignment,
  setalignment,
  color,
  setcolor,
  state,
  papers,
  stats,
}) {
  const {
    data: fonts,
    IsPending: fontsIspending,
    get: getfonts,
  } = useGetFetch();
  const {
    data: fontsizes,
    IsPending: fontsizesIspending,
    get: getfontsizes,
  } = useGetFetch();
  const {
    data: pageFormats,
    setdata: setpageFormats,
    IsPending: pageFormatsIspending,
    get: getpageFormats,
  } = useGetFetch();

  const {
    data: orientations,
    setdata: setorientations,
    IsPending: orientationsIspending,
    get: getorientations,
  } = useGetFetch();

  const {
    data: setting,
    setdata: setsetting,
    IsPending: settingIspending,
    get: getsetting,
  } = useGetFetch();
  const {
    data: version,
    setdata: setversion,
    IsPending: versionIspending,
    get: getversion,
  } = useGetFetch();

  const { data: firmwareData, post: updatefirmwarepost } = usePostFormData();
  useEffect(() => {
    getfonts("/fonts");
    getfontsizes("/fontsizes");
    getpageFormats("/pageformats");
    getorientations("/orientations");
    getsetting("/setting");
    getversion("/version");
  }, []);

  const {
    data: calibrateData,
    setdata: setcalibrateData,
    IsPending: calibrateIsPending,
    post: calibratePost,
  } = usePostFetch();

  const { data: putdata, IsPending: putIsPending, put } = usePutFetch();
  const { data, setdata, IsPending, post } = usePostFetch();
  const [powerOn, setpowerOn] = useState(false);
  const [PapersState, setPapersState] = useState(0);
  const [toggleSetting, settoggleSetting] = useState(false);

  const [isPageFormatCustom, setisPageFormatCustom] = useState(false);
  const [pageFormat, setpageFormat] = useState();
  const [orientation, setorientation] = useState();
  const [autoPageBreak, setautoPageBreak] = useState(false);
  const [width, setwidth] = useState();
  const [height, setheight] = useState();
  const [marginLeft, setmarginLeft] = useState();
  const [marginRight, setmarginRight] = useState();
  const [marginTop, setmarginTop] = useState();

  const [calbrateEn, setcalbrateEn] = useState(false);
  const [firmwarefile, setfirmwarefile] = useState(null);
  const firmwareref = useRef();

  useEffect(() => {
    if (setting) {
      setorientation(setting.orientation);
      setisPageFormatCustom(setting.isPageFormatCustom);
      setpageFormat(setting.pageFormat || "P");
      setautoPageBreak(setting.autoPageBreak);
      setwidth(setting.pageWidth);
      setheight(setting.pageHeight);
      setmarginLeft(setting.leftMargin);
      setmarginRight(setting.rightMargin);
      setmarginTop(setting.topMargin);
    }
  }, [setting]);

  useEffect(() => {
    if (state) {
      setpowerOn(state.state !== "Offline");
    }
    if (papers) {
      if (PapersState === 0) setPapersState(papers.papers);
    }
  }, [state, papers]);

  useEffect(() => {
    if (data) {
      toast(data, { type: "success" });
      setdata(null);
    }
  }, [data]);

  function power(poweron) {
    if (poweron) {
      post("/poweron");
    } else {
      post("/poweroff");
    }
  }

  function setpaper(papers) {
    if (papers > 0) {
      put("/papers", JSON.stringify({ papers: Number.parseInt(papers) }));
    }
  }
  function setInc() {
    post("/cartridges");
  }
  function fixPaperJam() {
    if (state) {
      if (state.state === "Paper_Jam") {
        post("/fixpaperjam");
      }
    }
  }
  function calibrate() {
    let payload = {};
    payload.orientation = orientation;
    payload.isPageFormatCustom = isPageFormatCustom;
    payload.pageFormat = pageFormat;
    payload.autoPageBreak = autoPageBreak;
    payload.pageWidth = Number.parseInt(width);
    payload.pageHeight = Number.parseInt(height);
    payload.leftMargin = Number.parseInt(marginLeft);
    payload.rightMargin = Number.parseInt(marginRight);
    payload.topMargin = Number.parseInt(marginTop);
    calibratePost("/calibrate", JSON.stringify({ payload }));
  }
  function reset() {
    calibratePost("/reset");
  }
  function updateFirmware() {
    const payload = new FormData();
    firmwarefile && payload.append("file", firmwarefile);
    updatefirmwarepost("/firmware", payload);
  }
  useEffect(() => {
    if (firmwareData) {
      setversion(firmwareData);
      setfirmwarefile(null);
      firmwareref.current && (firmwareref.current.value = "");
      toast("firmware updated to version " + firmwareData);
    }
  }, [firmwareData]);

  useEffect(() => {
    if (calibrateData) {
      setsetting(calibrateData);
      setcalibrateData(null);
      toast("calibrated successfully", { type: "success" });
    }
  }, [calibrateData]);

  useEffect(() => {
    if (setting) {
      if (
        isPageFormatCustom !== setting.isPageFormatCustom ||
        (isPageFormatCustom &&
          (height !== setting.pageHeight || width !== setting.pageWidth)) ||
        (!isPageFormatCustom && pageFormat !== setting.pageFormat) ||
        pageFormat !== setting.pageFormat ||
        autoPageBreak !== setting.autoPageBreak ||
        orientation !== setting.orientation ||
        marginLeft !== setting.leftMargin ||
        marginRight !== setting.rightMargin ||
        marginTop !== setting.topMargin
      ) {
        setcalbrateEn(true);
      } else {
        setcalbrateEn(false);
      }
    }
  }, [
    setting,
    isPageFormatCustom,
    pageFormat,
    orientation,
    autoPageBreak,
    width,
    height,
    marginLeft,
    marginRight,
    marginTop,
  ]);
  return (
    <div className=" h-screen w-full  border-r  border-gray-200 ">
      <div>
        {!toggleSetting ? (
          <div className="relative space-x-2 p-4 border-b border-gray-200 text-gray-200  font-semibold bg-zinc-700  flex justify-center items-center ">
            <i className="far fa-option"></i>
            <div className="text-lg ">Options</div>
            <div
              onClick={() => settoggleSetting((v) => !v)}
              className="absolute right-5"
            >
              <i className="fa-regular cursor-pointer fa-gear hover:rotate-45 transition duration-150 text-lg"></i>
            </div>
          </div>
        ) : (
          <div className=" relative space-x-2 p-4 border-b border-gray-200 text-gray-200  font-semibold bg-zinc-700  flex justify-center items-center ">
            <i className="fa-light fa-wrench"></i>{" "}
            <div className="text-lg ">Settings</div>
            <div
              onClick={() => settoggleSetting((v) => !v)}
              className="absolute right-5"
            >
              <i className="fa-regular cursor-pointer fa-gear hover:rotate-45 transition duration-150 text-lg"></i>
            </div>
          </div>
        )}
      </div>
      {toggleSetting ? (
        <div>
          <div className="flex justify-between  text-gray-700 px-10 py-3 space-x-4 items-center">
            <i className="fa-sharp fa-solid fa-arrows-rotate"></i>{" "}
            <div className="w-[150px] h-[50px] bg-gray-100 border border-gray-200 rounded-lg cursor-pointer ">
              <DropBox
                value={pageFormat}
                values={pageFormats}
                setvalue={setpageFormat}
              />
            </div>
          </div>
          <div className="flex justify-between  text-gray-700 px-10 py-3 space-x-4 items-center">
            <div>Left Margin</div>
            <input
              className="w-16 outline-none border border-gray-400 rounded-md text-center"
              type="number"
              value={marginLeft}
              onChange={(e) => setmarginLeft(e.target.value)}
            />
          </div>
          <div className="flex justify-between  text-gray-700 px-10 py-3 space-x-4 items-center">
            <div>Right Margin</div>
            <input
              className="w-16 outline-none border border-gray-400 rounded-md text-center"
              type="number"
              value={marginRight}
              onChange={(e) => setmarginRight(e.target.value)}
            />
          </div>
          <div className="flex justify-between  text-gray-700 px-10 py-3 space-x-4 items-center">
            <div>Top Margin</div>
            <input
              className="w-16 outline-none border border-gray-400 rounded-md text-center"
              type="number"
              value={marginTop}
              onChange={(e) => setmarginTop(e.target.value)}
            />
          </div>
          <div className="flex justify-between text-gray-700 px-10 py-3 space-x-2 items-center">
            <input
              type="checkbox"
              className="w-4 h-4"
              checked={isPageFormatCustom}
              onChange={(e) => setisPageFormatCustom(e.target.checked)}
            />
            <div className="">Custom Page Format</div>
          </div>
          {!isPageFormatCustom ? (
            <div className="flex justify-between  text-gray-700 px-10 py-3 space-x-4 items-center">
              <i className="fas fa-file-lines text-xl"></i>
              <div className="w-[150px] h-[50px] bg-gray-100 border border-gray-200 rounded-lg cursor-pointer ">
                <DropBox
                  value={orientation}
                  values={orientations}
                  setvalue={setorientation}
                />
              </div>
            </div>
          ) : (
            <>
              <div className="flex justify-between  text-gray-700 px-10 py-3 space-x-4 items-center">
                <div>Width</div>
                <input
                  className="w-16 outline-none border border-gray-400 rounded-md text-center"
                  type="number"
                  value={width}
                  onChange={(e) => setwidth(e.target.value)}
                />
              </div>
              <div className="flex justify-between  text-gray-700 px-10 py-3 space-x-4 items-center">
                <div>Height</div>
                <input
                  className="w-16 outline-none border border-gray-400 rounded-md text-center"
                  type="number"
                  value={height}
                  onChange={(e) => setheight(e.target.value)}
                />
              </div>
            </>
          )}
          <div className="flex justify-between text-gray-700 px-10 py-3 space-x-2 items-center">
            <input
              type="checkbox"
              className="w-4 h-4"
              checked={autoPageBreak}
              onChange={(e) => setautoPageBreak(e.target.checked)}
            />
            <div className="">Auto Page Brake</div>
          </div>

          <div className="flex justify-center text-gray-700 px-10 py-3 space-x-2 items-center">
            {calbrateEn ? (
              <div
                onClick={calibrate}
                className="p-2  w-24  flex justify-center cursor-pointer select-none space-x-2 bg-zinc-700 text-gray-200 rounded-md"
              >
                Calibrate
              </div>
            ) : (
              <div className="p-2  w-24  flex justify-center cursor-pointer select-none space-x-2 bg-gray-200  text-gray-500 rounded-md">
                Calibrate
              </div>
            )}
            <div
              onClick={reset}
              className="p-2 w-24  flex justify-center cursor-pointer select-none space-x-2 bg-zinc-700 text-gray-200 rounded-md"
            >
              Reset
            </div>
          </div>
          <div className="px-10 space-y-2 border-t mt-3">
            <div className="text-gray-800 mt-5">Update firmware</div>
            <div className="p-2 flex justify-center  space-x-2 bg-zinc-700 text-gray-200 rounded-md">
              <div>
                <input
                  ref={firmwareref}
                  type="file"
                  className="w-[100px] outline-none  rounded-lg text-zinc-700 text-center"
                  onChange={(e) => setfirmwarefile(e.target.files[0])}
                />
              </div>
              {firmwarefile && (
                <div
                  onClick={updateFirmware}
                  className="bg-gray-300 text-gray-800 px-2 rounded-md cursor-pointer select-none hover:bg-gray-200"
                >
                  update
                </div>
              )}
            </div>
          </div>
        </div>
      ) : (
        <div>
          <div className="border-b pb-4">
            <div className="flex justify-between text-gray-700 px-8 py-2 space-x-2 items-center">
              <i className=" far  fa-font"></i>
              <div className="w-[150px] h-[50px] bg-gray-100 border border-gray-200 rounded-lg cursor-pointer ">
                <DropBox value={font} values={fonts} setvalue={setfont} />
              </div>
            </div>
            <div className="flex justify-between text-gray-700 px-8 py-2  space-x-2 items-center">
              <i className="far  fa-text-size"></i>
              <div className="w-[150px] h-[50px] bg-gray-100 border border-gray-200 rounded-lg cursor-pointer ">
                {fontsizes && (
                  <DropBox
                    value={fontsize}
                    values={(() => {
                      let x = [];
                      for (let i = fontsizes?.from; i <= fontsizes.to; i++) {
                        x.push(i);
                      }
                      return x;
                    })()}
                    setvalue={setfontsize}
                  />
                )}
              </div>
            </div>
            <div className="flex  justify-between text-gray-700 px-12 py-2  space-x-2 items-center">
              <div className="p-1 cursor-pointer flex items-center bg-gray-100 rounded-md drop-shadow-sm border">
                <input
                  type="color"
                  value={color}
                  className="w-6 cursor-pointer"
                  onChange={(e) => setcolor(e.target.value)}
                />
              </div>
              <div
                onClick={() => setitalic((v) => !v)}
                className={`${
                  italic ? "text-gray-700  border-gray-400 " : " text-gray-400"
                } p-2 bg-gray-100 cursor-pointer  rounded-md drop-shadow-sm border`}
              >
                <i className="fa-solid fa-italic"></i>
              </div>
              <div
                onClick={() => setbold((v) => !v)}
                className={`${
                  bold ? "text-gray-700 border-gray-400" : " text-gray-400"
                } p-2 bg-gray-100 cursor-pointer  rounded-md drop-shadow-sm border`}
              >
                <i className="fa-solid fa-bold"></i>
              </div>
              <div
                onClick={() => setunderline((v) => !v)}
                className={`${
                  underline ? "text-gray-700 border-gray-400" : " text-gray-400"
                } p-2 bg-gray-100 cursor-pointer  rounded-md drop-shadow-sm border`}
              >
                <i className="far fa-underline"></i>
              </div>
            </div>
            <div className="flex  justify-between text-gray-700 px-12 py-3  space-x-2 items-center">
              <div
                onClick={() => setalignment("L")}
                className={`${
                  alignment === "L"
                    ? "text-gray-700 border-gray-400"
                    : " text-gray-400"
                } p-2 bg-gray-100 cursor-pointer  rounded-md drop-shadow-sm border`}
              >
                <i className="fa-regular fa-align-left"></i>
              </div>
              <div
                onClick={() => setalignment("C")}
                className={`${
                  alignment === "C"
                    ? "text-gray-700  border-gray-400 "
                    : " text-gray-400"
                } p-2 bg-gray-100 cursor-pointer  rounded-md drop-shadow-sm border`}
              >
                <i className="fa-regular fa-align-center"></i>
              </div>
              <div
                onClick={() => setalignment("R")}
                className={`${
                  alignment === "R"
                    ? "text-gray-700 border-gray-400"
                    : " text-gray-400"
                } p-2 bg-gray-100 cursor-pointer  rounded-md drop-shadow-sm border`}
              >
                <i className="fa-regular fa-align-right"></i>
              </div>
            </div>
          </div>
          <div className="p-5 space-y-4">
            <div className="flex items-center space-x-2">
              <div className="text-gray-800">Power</div>
              <div
                onClick={() =>
                  setpowerOn((v) => {
                    v = !v;
                    power(v);
                    return v;
                  })
                }
                className={`${
                  powerOn ? "bg-blue-400" : "bg-gray-300"
                } w-[42px]  px-[4px] py-[2px] flex items-center rounded-full  ease-linear duration-150 `}
              >
                <div
                  className={`${
                    powerOn ? "powerOn translate-x-[15px]" : ""
                  } w-[18px] transition delay-75 ease-linear duration-200 h-[18px] bg-white rounded-full`}
                ></div>
              </div>
            </div>
            <div className="p-2 flex space-x-2 bg-zinc-700 text-gray-200 rounded-md">
              <div>Set Papers</div>
              <div>
                <input
                  type="number"
                  className="w-16 outline-none  rounded-lg text-zinc-700 text-center"
                  onChange={(e) =>
                    setPapersState(e.target.value >= 0 ? e.target.value : 0)
                  }
                  value={PapersState}
                />
              </div>
              <div
                onClick={() => setpaper(PapersState)}
                className="bg-gray-300 text-gray-800 px-4 rounded-md cursor-pointer select-none hover:bg-gray-200"
              >
                Set
              </div>
            </div>
            {state?.state === "Paper_Jam" ? (
              <div
                onClick={fixPaperJam}
                className="p-2 flex cursor-pointer select-none space-x-2 bg-zinc-700 text-gray-200 rounded-md"
              >
                Fix PaperJam
              </div>
            ) : (
              <div className="p-2 flex cursor-pointer select-none space-x-2 bg-gray-300 text-gray-500 rounded-md">
                Fix PaperJam
              </div>
            )}
            <div
              onClick={setInc}
              className="p-2 flex cursor-pointer select-none space-x-2 bg-zinc-700 text-gray-200 rounded-md"
            >
              Replace Cartridges
            </div>
          </div>

          <div>
            <div className="text-gray-700 px-2">
              <div>Version : {version}</div>
            </div>
            <div className="text-gray-700 px-2">
              Last Run : {stats?.LastRun}
            </div>
            <div className="text-gray-700 px-2">
              Last Print : {stats?.LastPrint}
            </div>
            <div className="text-gray-700 px-2">
              Printed Docs : {stats?.Count}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default Setting;
