// @ts-nocheck
import React, { useEffect, useState } from 'react'
import { useRef } from 'react';
import { toast } from 'react-toastify';

const usePutFetch = () => {
  const [data, setdata] = useState(null);
  const [err, seterr] = useState(null);
  const [IsPending, setIsPending] = useState(false);
  const abortcont = useRef();

  useEffect(() => {
    abortcont.current = new AbortController();
    return () => {
      abortcont.current && abortcont.current.abort();
    };
  }, []);
 useEffect(() => {
   if (err) {
    console.log(err)
     toast(err.message, { type: "error" });
   }
 }, [err]);

  const put = async (path,body) => {
    setIsPending(true);
    const url = process.env.REACT_APP_SERVERURL + path;
    let res;
    try {
      res = await fetch(url, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body,
        signal: abortcont.current?.signal,
      });

      if (res.ok) {
        const resdata = await res.text();
        console.log("from getfetch " + resdata);

        try {
          let data = JSON.parse(resdata);
          setdata(data);
        } catch (e) {
          setdata(resdata);
        }
        setIsPending(false);
      } else {
        const resdata = await res.text();
        try {
          const jsonResdata = JSON.parse(resdata);
          console.log(jsonResdata);
          seterr(jsonResdata);
          setIsPending(false);
        } catch (e) {
          seterr({ message: resdata });
          setIsPending(false);
        }
      }
    } catch (e) {
      console.log(e.name);
    }
  };
  return { data, setdata, IsPending, err, put };
};
export default usePutFetch