import React from "react";

function FinishedTask({finishedTasks}) {
  return finishedTasks.map((task, index) => (
    <div className="flex items-center p-2 rounded-md text-gray-700 border bg-white drop-shadow-md">
      <div className=" bg-blue-400 flex items-center justify-center w-10 h-10 rounded-full">
        #{index}
      </div>
      <div className="rounded-full   px-4 p-[2px]">
        <div className="font-semibold">{task.id}</div>
        <div className="flex items-center space-x-2">
          <div
            className=" bg-pink-600 rounded-full w-3 h-3"
          ></div>

          <div>{task.state}</div>
        </div>
      </div>
      <div>
        <a
          href={process.env.REACT_APP_SERVERURL+"/pdf/" + task.id}
          target="_blank"
          download
        >
          <i className="fas fa-download text-blue-500 drop-shadow-lg text-lg cursor-pointer"></i>
        </a>
      </div>
    </div>
  ));
}

export default FinishedTask;
