// @ts-nocheck
import { useEffect, useRef, useState } from "react";
import { toast } from "react-toastify";

const useGetFetch = () => {
  const [data, setdata] = useState(null);
  const [err, seterr] = useState(null);
  const [IsPending, setIsPending] = useState(false);
  const abortcont = useRef();

  useEffect(() => {
    abortcont.current = new AbortController();
    return () => {
      abortcont.current && abortcont.current.abort();
    };
  }, []);

  useEffect(()=>{
    if(err){
      toast(err.message,{type:"error"})
    }
  },[err])

  const get = async (path) => {
    setIsPending(true);
    const url = process.env.REACT_APP_SERVERURL + path;
    let res;
    try {
      res = await fetch(url, {
        method: "GET",
        signal: abortcont.current?.signal,
      });

      if (res.ok) {
        const resdata = await res.text();
        console.log("from getfetch " + resdata);

        try {
          let data = JSON.parse(resdata);
          setdata(data);
        } catch (e) {
          setdata(resdata);
        }
        setIsPending(false);
      } else {
        const resdata = await res.text();
        try {
          const jsonResdata = JSON.parse(resdata);
          console.log(jsonResdata);
          seterr(jsonResdata);
          setIsPending(false);
        } catch (e) {
          seterr({ message: resdata });
          setIsPending(false);
        }
      }
    } catch (e) {
      console.log(e.name);
    }
  };
  return { data,setdata, IsPending, err, get };
};

export default useGetFetch;
