// @ts-nocheck
import React, { useEffect, useRef, useState } from "react";

function DropBox({value,setvalue,values}) {
  const [visable, setvisable] = useState(false);
  const DropRef = useRef(null)
   useEffect(() => {
   function handle(e) {
     if (DropRef.current && DropRef.current?.contains(e.target)) return;
     setvisable(false);
   }
   document.addEventListener("click", handle);
   return () => {
     document.removeEventListener("click", handle);
   };
 }, []);
  return (
    <div
      ref={DropRef}
      className="relative space-y-1 w-full h-full  justify-between "
    >
      <div
        onClick={() => setvisable((v) => !v)}
        className="flex space-x-2 h-full justify-center items-center "
      >
        <div>{value}</div>
        <i className="fa-regular fa-angle-down"></i>
      </div>
      {visable && (
        <div className="absolute w-[150px] max-h-[200px] overflow-y-auto border drop-shadow-lg bg-white z-10 ">
          {values.map((val) => (
            <div
              key={val}
              onClick={() => {
                setvalue(val);
                setvisable(false);
              }}
              className="p-2 text-center hover:bg-gray-100 border"
            >
              {val}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default DropBox;
