// @ts-nocheck
import React, { useEffect, useState } from "react";
import { useRef } from "react";
import { toast } from "react-toastify";

const usePostFormData = () => {
  const [data, setdata] = useState(null);
  const [err, seterr] = useState(null);
  const [IsPending, setIsPending] = useState(false);
  const abortcont = useRef();

  useEffect(() => {
    abortcont.current = new AbortController();
    return () => {
      abortcont.current && abortcont.current.abort();
    };
  }, []);
  useEffect(() => {
    if (err) {
      console.log(err);
      toast(err.message, { type: "error" });
    }
  }, [err]);

  const post = async (path, body) => {
    setIsPending(true);
    const url =process.env.REACT_APP_SERVERURL +path;
    let res;
    try {
      res = await fetch(url, {
        method: "POST",
        body,
        signal: abortcont.current?.signal,
      });

      if (res.ok) {
        const resdata = await res.text();
        console.log("from getfetch " + resdata);

        try {
          let data = JSON.parse(resdata);
          setdata(data);
        } catch (e) {
          setdata(resdata);
        }
        setIsPending(false);
      } else {
        const resdata = await res.text();
        try {
          const jsonResdata = JSON.parse(resdata);
          console.log(jsonResdata);
          seterr(jsonResdata);
          setIsPending(false);
        } catch (e) {
          seterr({ message: resdata });
          setIsPending(false);
        }
      }
    } catch (e) {
      console.log(e.name);
    }
  };
  return { data, setdata, IsPending, err, post };
};
export default usePostFormData;
