import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import usePostFetch from "../customHooks/usePostFetch";
import FinishedTask from "./FinishedTask";
import PendingTask from "./PendingTask";

function Tasks({ pendingTasks, finishedTasks, state }) {
  const [TasksFilter, setTasksFilter] = useState("Pending");
  const { data, setdata, post } = usePostFetch();
  useEffect(() => {
    if (data) {
      toast(data, { type: "success" });
      setdata(null);
    }
  }, [data]);
  function cancel() {
    post("/cancel");
  }
  function pause() {
    if (state && state.state !== "Paused") {
      post("/pause");
    }
  }
  function resume() {
    if (state && state.state === "Paused") {
      post("/resume");
    }
  }
  return (
    <div className="w-full h-screen border-[1px] border-t-0  bg-white border-gray-200 ">
      <div className="space-y-2">
        <div className=" space-x-2 p-4 border-b border-gray-200 text-gray-200  font-semibold bg-zinc-700  flex justify-center items-center ">
          <i className="far fa-bars-progress"></i>
          <div className="text-lg ">Tasks</div>
        </div>
        <div className="flex justify-center  space-x-3 mt-3">
          <div
            onClick={() => setTasksFilter("Pending")}
            className={`${
              TasksFilter === "Pending"
                ? "text-gray-800 border-gray-400"
                : "text-gray-500"
            } p-2 bg-gray-100 border select-none rounded-lg cursor-pointer`}
          >
            Pending
          </div>
          <div
            onClick={() => setTasksFilter("Finished")}
            className={`${
              TasksFilter === "Finished"
                ? "text-gray-800 border-gray-400"
                : "text-gray-500"
            } p-2 bg-gray-100 border select-none rounded-lg cursor-pointer`}
          >
            Finished
          </div>
        </div>
        <div className="p-5 h-[450px] border-b border-t  overflow-y-auto space-y-4">
          {TasksFilter === "Pending" ? (
            <PendingTask pendingTasks={pendingTasks} />
          ) : (
            <FinishedTask finishedTasks={finishedTasks} />
          )}
        </div>
        <div className="p-4 flex justify-between ">
          <div
            onClick={cancel}
            className="px-2 py-3 select-none cursor-pointer bg-red-400   rounded-md w-max"
          >
            cancel
          </div>
          <div
            onClick={pause}
            className="px-2 py-3 select-none cursor-pointer bg-yellow-400 rounded-md w-max"
          >
            pause
          </div>
          <div
            onClick={resume}
            className="px-2 py-3 select-none cursor-pointer bg-green-400 rounded-md w-max"
          >
            resume
          </div>
        </div>
      </div>
    </div>
  );
}

export default Tasks;
