import React, { useEffect } from "react";
import { toast } from "react-toastify";
import usePostFetch from "../customHooks/usePostFetch";

function PendingTask({ pendingTasks }) {
  const { data, setdata, post } = usePostFetch();
  useEffect(() => {
    if (data) {
      toast(data, { type: "success" });
      setdata(null);
    }
  }, [data]);
  function cancelTask(taskid) {
    post("/canceltask", JSON.stringify({ taskid }));
  }

  return pendingTasks.map((task, index) => (
    <div
      key={task.id}
      className="flex items-center p-2 rounded-md text-gray-700 border bg-white drop-shadow-md"
    >
      <div className=" bg-blue-400 flex items-center justify-center w-10 h-10 rounded-full">
        #{index}
      </div>
      <div className="rounded-full   px-4 p-[2px]">
        <div className="font-semibold">{task.id}</div>
        <div className="flex items-center space-x-2">
          <div
            className={`${
              task.state === "Wating" ? "bg-amber-400" : "bg-green-400"
            } rounded-full w-3 h-3`}
          ></div>

          <div>{task.state}</div>
        </div>
      </div>
      {task.state === "Wating" && (
        <div
          onClick={() => cancelTask(task.id)}
          className=" absolute right-[-10px] top-[-10px] rounded-full w-7 h-7 flex items-center justify-center  bg-red-400"
        >
          <i className="fa-solid fa-xmark"></i>
        </div>
      )}
    </div>
  ));
}

export default PendingTask;
