import React, { useEffect, useRef, useState } from "react";

function Dragdrop({file,setfile}) {
  const [DragOver, setDragOver] = useState(false);
  const fileInput=useRef(null)

  function OnDragOver(e) {
    e.preventDefault();
    e.stopPropagation();
    setDragOver(true);
  }
  function OnDragLeave(e) {
    e.preventDefault();
    e.stopPropagation();
    setDragOver(false);
  }
  function OnDrop(e) {
    e.preventDefault();
    e.stopPropagation();
    setfile(e.dataTransfer.files[0]);
    setDragOver(false)
  }
  function OnClick(e){
    // @ts-ignore
    fileInput.current&&fileInput.current.click();
  }
  return (
    <div
      onClick={OnClick}
      onDragOver={OnDragOver}
      onDragLeave={OnDragLeave}
      onDrop={OnDrop}
      className="flex justify-center items-center w-full h-full cursor-pointer select-none"
    >
      <div
        className={`${
          DragOver
            ? "border-blue-500 scale-[1.02]"
            : file
            ? "border-blue-500 "
            : "border-zinc-500 "
        } border-[3px] w-11/12 h-4/5 border-dashed flex justify-center items-center text-gray-800 rounded-xl`}
      >
        <div className="text-center">
          <div>Drag Drop Or Click To Add Text File</div>
          {file && (
            <div>
              {
                // @ts-ignore
                file.name
              }
            </div>
          )}
        </div>
        <input
          ref={fileInput}
          type="file"
          className="hidden"
          // @ts-ignore
          onChange={(e) => setfile(e.target.files[0])}
        />
      </div>
    </div>
  );
}

export default Dragdrop;
