import React, { useState } from "react";
import Dragdrop from "./Dragdrop";
import TextForm from "./TextForm";

function Printbox({ text, settext, print, printfile, file, setfile }) {
  const [printobj, setprintobj] = useState("text");

  return (
    <div className="m-4 space-y-3">
      <div className="flex  w-max rounded-lg overflow-clip cursor-pointer select-none">
        <div
          onClick={() => setprintobj("text")}
          className={`${
            printobj === "text" ? "bg-blue-400 " : "bg-gray-200 "
          }text-center w-24 text-gray-800   p-4`}
        >
          text
        </div>
        <div
          onClick={() => setprintobj("file")}
          className={`${
            printobj === "file" ? "bg-blue-400  " : "bg-gray-200 "
          }text-center w-24 text-gray-800  p-4`}
        >
          file
        </div>
      </div>
      <div className="p-4 bg-gray-100 w-full drop-shadow-md  rounded-lg h-[400px]">
        {printobj === "text" ? (
          <TextForm text={text} settext={settext} />
        ) : (
          <Dragdrop file={file} setfile={setfile} />
        )}
      </div>
      <div className="flex justify-center">
        {printobj === "text" ? (
          text.length > 0 ? (
            <div
              onClick={print}
              className="rounded-md py-3 px-5 cursor-pointer select-none  mt-4  bg-blue-400"
            >
              print
            </div>
          ) : (
            <div className="rounded-md py-3 px-5 cursor-pointer select-none  mt-4 text-gray-500  bg-blue-300">
              print
            </div>
          )
        ) : file ? (
          <div
            onClick={printfile}
            className="rounded-md py-3 px-5 cursor-pointer  select-none mt-4  bg-blue-400"
          >
            print
          </div>
        ) : (
          <div className="rounded-md py-3 px-5 cursor-pointer select-none  mt-4 text-gray-500  bg-blue-300">
            print
          </div>
        )}
      </div>
    </div>
  );
}

export default Printbox;
